<template>
  <div class="citybox">
    <mt-header title="选择地区">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="top">
      <img class="image" src="../assets/adress.png" alt="">
      <div class="p" v-if="address==''">正在定位...</div>
      <div class="p" v-else>{{address}}</div>
    </div>
    <el-amap vid="amap" :plugin="plugin" class="amap-demo" :center="center">
    </el-amap>
    <ul>
      <li v-for="item in cityList" @click="listClick(item)">{{item.CityName}}</li>
    </ul>
  </div>
</template>

<script>
    import { Indicator,Toast } from 'mint-ui'
    const axios = require('axios')
     export default{
      data () {
        let self = this
        return {
          center: [121.59996, 31.197646],
          lng: 0,
          lat: 0,
          loaded: false,
          address: '',
          cityList: [],
          plugin: [{
            pName: 'Geolocation',
            events: {
              init(o) {
                // o 是高德地图定位插件实例
                o.getCurrentPosition((status, result) => {
                  if (result && result.position) {
                    console.log(result)
                    self.lng = result.position.lng
                    self.lat = result.position.lat
                    self.center = [self.lng, self.lat]
                    self.loaded = true
                    self.address = result.addressComponent.province + result.addressComponent.city + result.addressComponent.district
                    self.$nextTick()
                  }
                })
              }
            }
          }]
        }
      },
      methods: {
        listClick(item) {
          sessionStorage.setItem("CityID",item.CityID);
          this.$router.push({
  						path: '/home'
  					})
        },
        GetCityList() {
          axios({
            method: 'post',
            url: 'https://www.c65z45.cn/api/Reserve/GetCityList',
            data: {
              Token: 'B68DE9D1F8584D7099BCE47144D0351C'
            }
          }).then(res => {
            Indicator.close()
            if(res.data.IsSuccess){
              this.cityList = res.data.Data
            }else{
              Toast({
                message: '加载失败',
                position: 'middle',
                duration: 3000
              })
            }
          }).catch(error => {
            Indicator.close()
            Toast({
              message: '服务器错误',
              position: 'middle',
              duration: 3000
            })
          })
        }
      },
      created() {
        this.GetCityList()
      },
      mounted(){
        Indicator.open('加载中...')
      }
    }
</script>

<style scoped>
  .mint-header {
    background: rgb(0, 204, 153);
  }
  ul{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0.3rem;
    box-sizing: border-box;
    flex-wrap:wrap;
  }
  ul>li{

    width: 2rem;
    height: 0.5rem;
    line-height: 0.5rem;
    font-size: 0.5rem;
    text-align: center;
    background: #00cc99;
    color: #fff;
    padding: 0.1rem;
    border-radius: 0.1rem;
    margin: 0.2rem 0;
  }
  .top {
    width: 100%;
    height: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    border-bottom: 0.02rem solid #ccc;
  }

  .top .p {
    font-size: 0.25rem;
    color: rgb(40, 177, 160);
  }

  .top .image {
    width: 0.45rem;
    height: 0.47rem;
    padding-right: 0.1rem;

  }
</style>
